  editInfo() {
    if (!this.display2) {
      this.msgs = [];
      this.msgs.push({ severity: 'error', summary: '', detail: '' });
      return false;
    }

    this.canEdite = false;
  }
  savaInfo() {
    //
    for (const i in this.userform.controls) {
      this.userform.controls[i].markAsDirty();
    }
    //
    for (const i in this.userform.controls) {
      if (!this.userform.controls[i].valid) {
        return;
      }
    }
    
    this.canEdite = true;
    let params = {
      groupId: this.groupId,
      custListNum: 1,
      custMap: this.custInfo,
      flagId: '1'
    }
    this.httpService.updateCustGroupList(params).subscribe(data => {
      if (data.returnCode.code == 'success') {
        this.selectGroupEvalInfoBean = data;
        this.selectGroupEvalInfoBean.evalList = data.evalList;
        // 
        this.list = data.evalList;
        this.total = data.totalNum;
        this.msgs = [];
        this.msgs.push({ severity: 'success', summary: '', detail: "" });
      } else {
        this.msgs = [];
        this.msgs.push({ severity: 'error', summary: '', detail: data.returnCode.message });
      }
    }, error => {
      this.msgs = [];
      this.msgs.push({ severity: 'error', summary: '', detail: '' });
    })
  }
