  selectFreeGroupInfo() {
    let params = {
      groupId: this.groupId
    }
    // 
    this.httpService.selectFreeGroupInfo(params).subscribe(data => {
      if (data.returnCode.code == 'success') {
        this.title = `${data.groupName}-${this.custInfo.custName}`;

        let mesg = JSON.parse(this.commfunc.getSessionData('commonHeader'));
        if (mesg.userId == data.ownerId) {
          this.display2 = true;
        }
      } else {
        this.msgs = [];
        data.returnCode.code ? data.returnCode.code : '';
        this.msgs = [{ severity: 'error', summary: '', detail: data.returnCode.message }];
      }
    }, err => {
      this.msgs = [];
      this.msgs = [{ severity: 'error', summary: '', detail: '' }];
    });
  }

